<template>
	<view class="loading">
		<view class="loading-item loading-item-01"></view>
		<view class="loading-item loading-item-02"></view>
		<view class="loading-item loading-item-03"></view>
		<view class="loading-item loading-item-04"></view>
		<view class="loading-item loading-item-05"></view>
	</view>
</template>


<style>
	.loading {
		width: 300rpx;
		height: 40rpx;
		margin: 0 auto;
		text-align: center;
	}

	.loading .loading-item {
		display: inline-block;
		width: 15px;
		height: 100%;
		margin-right: 5px;
		background: lightgreen;
		-webkit-animation: load 1.04s ease infinite;
	}

	.loading .loading-item-05 {
		margin-right: 0px;
	}

	@-webkit-keyframes load {
		0% {
			opacity: 1;
		}

		100% {
			opacity: 0;
		}
	}

	.loading .loading-item-01 {
		-webkit-animation-delay: 0.13s;
	}

	.loading .loading-item-02 {
		-webkit-animation-delay: 0.26s;
	}

	.loading .loading-item-03 {
		-webkit-animation-delay: 0.39s;
	}

	.loading .loading-item-04 {
		-webkit-animation-delay: 0.52s;
	}

	.loading .loading-item-05 {
		-webkit-animation-delay: 0.65s;
	}
</style>
